import * as React from "react";
import { StyleSheet } from "react-native";

import { ListItem } from "react-native-elements";

import { Text } from "react-native-elements";

export default function InsiderTransaction(props: {
  i: number;
  code: string;
  owner: string;
  exchange: string;
  date: string;
  transactionAmount: number;
  transactionPrice: number;
}) {
  return (
    <ListItem key={props.i} bottomDivider>
      <ListItem.Content>
        <ListItem.Title style={styles.title}>{props.code} - {props.owner}</ListItem.Title>
        <Text>Exchange: {props.exchange}</Text>
        <Text>Date: {props.date}</Text>
        <Text>Transaction Amount: {props.transactionAmount}</Text>
        <Text>Transaction Price: {props.transactionPrice}</Text>
      </ListItem.Content>
    </ListItem>
  );
}

const styles = StyleSheet.create({
  title: {
    fontWeight: "bold",
  },
});
