import { FontAwesome } from "@expo/vector-icons";
import { createBottomTabNavigator } from "@react-navigation/bottom-tabs";
import {
  NavigationContainer,
  DefaultTheme,
  DarkTheme,
} from "@react-navigation/native";
import { createNativeStackNavigator } from "@react-navigation/native-stack";
import * as React from "react";
import { ColorSchemeName, Pressable } from "react-native";

import Colors from "../constants/Colors";
import useColorScheme from "../hooks/useColorScheme";
import ModalScreen from "../screens/ModalScreen";
import NotFoundScreen from "../screens/NotFoundScreen";
import TabOneScreen from "../screens/TabOneScreen";
import PreMarketScreen from "../screens/PreMarketScreen";
import PostMarketScreen from "../screens/PostMarketScreen";
import InsiderTransactionsScreen from "../screens/InsiderTransactionsScreen";
import TabThreeScreen from "../screens/TabThreeScreen";
import {
  RootStackParamList,
  RootTabParamList,
  RootTabScreenProps,
} from "../types";
import LinkingConfiguration from "./LinkingConfiguration";
import { Icon } from "react-native-elements";

export default function Navigation({
  colorScheme,
}: {
  colorScheme: ColorSchemeName;
}) {
  return (
    <NavigationContainer
      linking={LinkingConfiguration}
      theme={colorScheme === "dark" ? DarkTheme : DefaultTheme}
    >
      <RootNavigator />
    </NavigationContainer>
  );
}

/**
 * A root stack navigator is often used for displaying modals on top of all other content.
 * https://reactnavigation.org/docs/modal
 */
const Stack = createNativeStackNavigator<RootStackParamList>();

function RootNavigator() {
  return (
    <Stack.Navigator>
      <Stack.Screen
        name="Root"
        component={BottomTabNavigator}
        options={{ headerShown: false }}
      />
      <Stack.Screen
        name="NotFound"
        component={NotFoundScreen}
        options={{ title: "Oops!" }}
      />
      <Stack.Group screenOptions={{ presentation: "modal" }}>
        <Stack.Screen name="Modal" component={ModalScreen} />
      </Stack.Group>
    </Stack.Navigator>
  );
}

/**
 * A bottom tab navigator displays tab buttons on the bottom of the display to switch screens.
 * https://reactnavigation.org/docs/bottom-tab-navigator
 */
const BottomTab = createBottomTabNavigator<RootTabParamList>();

function BottomTabNavigator() {
  const colorScheme = useColorScheme();

  return (
    <BottomTab.Navigator
      initialRouteName="TabOne"
      screenOptions={{
        tabBarActiveTintColor: Colors[colorScheme].tint,
      }}
    >
      <BottomTab.Screen
        name="TabOne"
        component={TabOneScreen}
        options={({ navigation }: RootTabScreenProps<"TabOne">) => ({
          title: "",
          headerTitle: "Stocks",
          headerRight: () => <Icon reverse name="add" type="ionicon" />,
          tabBarIcon: ({ color }) => (
            <TabBarIcon name="line-chart" color={color} />
          ),
        })}
      />
      <BottomTab.Screen
        name="InsiderTransactions"
        component={InsiderTransactionsScreen}
        options={({
          navigation,
        }: RootTabScreenProps<"InsiderTransactions">) => ({
          title: "",
          headerTitle: "Insider Transactions",
          tabBarIcon: ({ color }) => (
            <TabBarIcon name="get-pocket" color={color} />
          ),
        })}
      />
      <BottomTab.Screen
        name="PreMarket"
        component={PreMarketScreen}
        options={({ navigation }: RootTabScreenProps<"PreMarket">) => ({
          title: "",
          headerTitle: "Pre-Market",
          tabBarIcon: ({ color }) => (
            <TabBarIcon name="backward" color={color} />
          ),
        })}
      />
      <BottomTab.Screen
        name="PostMarket"
        component={PostMarketScreen}
        options={({ navigation }: RootTabScreenProps<"PostMarket">) => ({
          title: "",
          headerTitle: "Post-Market",
          tabBarIcon: ({ color }) => (
            <TabBarIcon name="forward" color={color} />
          ),
        })}
      />
      <BottomTab.Screen
        name="TabThree"
        component={TabThreeScreen}
        options={{
          title: "",
          headerTitle: "Profile",
          tabBarIcon: ({ color }) => <TabBarIcon name="user" color={color} />,
        }}
      />
    </BottomTab.Navigator>
  );
}

/**
 * You can explore the built-in icon families and icons on the web at https://icons.expo.fyi/
 */
function TabBarIcon(props: {
  name: React.ComponentProps<typeof FontAwesome>["name"];
  color: string;
}) {
  return <FontAwesome size={30} style={{ marginBottom: -3 }} {...props} />;
}
