import * as React from "react";
import { StyleSheet } from "react-native";

import { ListItem, Icon } from "react-native-elements";

import { Text } from "react-native-elements";

export default function Stock(props: {
  i: number;
  name: string;
  sub: string;
  up: number;
  price: string;
  defrence: string;
}) {
  return (
    <>
      <ListItem key={props.i} bottomDivider>
        <ListItem.Content>
          <ListItem.Title>{props.name}</ListItem.Title>
          <ListItem.Subtitle>{props.sub}</ListItem.Subtitle>
        </ListItem.Content>
        <Text>{props.price}</Text>
        <Icon
          name={props.up ? "trending-up" : "trending-down"}
          color={props.up ? "green" : "red"}
        />
        <Text style={{ color: props.up ? "green" : "red" }}>
          {props.defrence}
        </Text>
      </ListItem>
    </>
  );
}

const styles = StyleSheet.create({
  title: {
    fontWeight: "bold",
  },
});
